@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500;600;700;800;900&display=swap);
@import url(https://clean-u-i.netlify.app/component/component.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: #fc3936;
  --background-color: #121212;
  --secondary-color: #070708;
  --text-color: #f1efef;
  --accent-color: #3700b3;
  --success-color: #3ded1d;
  --error-color: #f20b07;
  --shadow-color: #0066ff;
  --search-bar-color: rgb(221, 221, 221);
  --dark-color: black;
  --shadow-color1:rgba(1,1,1, 0.432);
  --shadow-color2:rgba(255,255,255, 0.432);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
}
body {
  background-color: #121212;
  background-color: var(--background-color);
  color: #f1efef;
  color: var(--text-color);
}
a {
  text-decoration: none;
}
button {
  border: none;
}
.text-orange {
  color: #fc3936;
  color: var(--primary-color);
  font-family: 'Akaya Telivigala', cursive;
}
.mb-3 {
  margin-bottom: 2rem;
}
.my-3 {
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.p-3 {
  padding: 2rem;
}
.orange-button {
  background-color: #fc3936;
  background-color: var(--primary-color);
}
.justify-between {
  justify-content: space-between;
}
.justify-center {
  justify-content: center;
}
.justify-around {
  justify-content: space-around;
}
.align-center {
  align-items: center;
}
.space-1rem {
  width: 100%;
  height: 1rem;
}
.space-2rem {
  width: 100%;
  height: 2rem;
}
.space-3rem {
  width: 100%;
  height: 3rem;
}
.space-4rem {
  width: 100%;
  height: 4rem;
}
.logo-text {
  font-family: "Akaya Telivigala", cursive;
}
button:hover {
  background-color: var(--light-color);
  color: #3700b3;
  color: var(--accent-color);
}
.primary-button-blue {
  background-color: #3700b3;
  background-color: var(--accent-color);
  color: var(--light-color);
}

input[type="radio"] {
  width: 1rem;
  height: 1rem;
  align-items: center;
}







.homePage-container {
    display: grid;
    grid-template-rows: 6.25rem auto;
  }
  .sidebar {
    grid-column-start: 1;
    grid-column-end: 2;
    position: relative;
    box-shadow: var(--shadow-color) 0px 0px 15px 0px;
    background-color: var(--secondary-color);
    border-radius: 1rem;
    margin: 2rem 0 2rem 2rem;
  }
  .sidebar-list-item {
    position: relative;
    text-align: center;
    background-color:var(--dark-color);
    color: var(--light-color);
    display: block;
    width: 100%;
    cursor:pointer;
    border-radius:2rem;
  }
  .active{
    background-color: var(--accent-color);
    border-radius: 2rem;
  
  }
  .logout {
    position: absolute;
    bottom: 32px;
    left: 34%;
    border-radius: 1rem;
    background-color: var(--accent-color);
    color: var(--light-color);
    padding: 0.5rem 1rem;
    transition: all ease-in-out 0.3s;
  }
  .logout:hover{
    background-color:var(--light-color);
    color:var(--accent-color);
  }
  .main-container {
    display: grid;
    grid-template-columns: 18.75rem auto;
  }
  .main-content-container {
    grid-column-start: 2;
    grid-column-end: 3;
    background-color: var(--secondary-color);
    box-shadow: var(--shadow-color) 0px 0px 15px 0px;
    border-radius: 1rem;
    margin: 2rem;
  }
  .main-content-section1-container {
    flex: 0.5 1;
    max-width: 80rem;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .user-image-container {
    flex: 0.3 1;
  }
  .user-image-container img {
    width: 10rem;
    height: 10rem;
  }
  .details-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1 1;
  }
  .main-content-section2-container {
    flex: 1 1;
    max-width: 80rem;
  }
  .details-section2-container {
    grid-gap: 1rem;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 50rem;
  }
  .score-container {
    box-shadow: var(--shadow-color) 0px 0px 0px 2px,
    var(--shadow-color) 0px 4px 6px -1px,
    var(--shadow-color) 0px 1px 0px inset;
    grid-gap: 1rem;
    gap: 1rem;
    padding: 1rem;
    align-items: center;
    border-radius: 1rem;
  }
  .main-content-section2-container {
    grid-gap: 1rem;
    gap: 1rem;
  }
  .user-achievements-container {
    flex: 0.5 1;
    box-shadow: var(--shadow-color) 0px 0px 0px 2px,
    var(--shadow-color) 0px 4px 6px -1px,
    var(--shadow-color) 0px 1px 0px inset;
    border-radius: 1rem;
  }
  .quiz-categories-container {
    flex: 0.5 1;
    border-radius: 1rem;
  }
  .main-content-section2-card-container {
    flex: 0.5 1;
    max-width: 37.5rem;
  }
  .trophy-container {
    grid-gap: 1rem;
    gap: 1rem;
    text-align: center;
    width: 6.25rem;
    margin: auto;
  }
  .component-image-square img {
    border-radius: 1rem;
    object-fit: cover;
  }
  .quiz-category-image {
    position: relative;
    width: 12.5rem;
    cursor:pointer;
    overflow:hidden;
    border-radius: 1rem;
  
  }
  .quiz-category-image p {
    position: absolute;
    bottom: 10px;
    left: 10px;
    font-weight: 600;
  }
  .component-image-square img:hover{
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
    transition:all ease-out 1s;
  }
  @media only screen and (max-width: 1150px) {
    .main-content-section2-container {
      flex-direction: column;
    }
    .details-section2-container {
      justify-content: space-around;
    }
    .main-content-section2-card-container {
      max-width: 100%;
    }
  }
  @media only screen and (max-width: 900px) {
    .sidebar {
      display: none;
    }
    .main-content-container {
      grid-column-start: 1;
      grid-column-end: 3;
    }
    .quiz-category-image {
      width: 100%;
    }
    .search-bar-container {
      width: 10rem;
    }
  }
  @media only screen and (max-width: 540px) {
    .logo-text{
      font-size: 1.5rem;
    }
    .logo-image{
      width:3rem;
      height:3rem;
    }
    .user-image {
      display: none;
      flex: 0 1;
    }
    .user-image-container {
      flex: 0 1;
    }
    .details-section1-container {
      display: none;
    }
    .search-bar-container {
      width: 5rem;
    }
  }
  
.homePage-container {
    display: grid;
    grid-template-rows: 6.25rem auto;
  }
.header {
    grid-row-start: 1;
    grid-row-end: 2;
    background-color: var(--secondary-color);
  }
  .logo-image{
    width:4rem;
    height:4rem;
  }
  .logo-text{
    font-size: 3rem;
  }
  .search-bar-container {
    display: flex;
    width: 20rem;
    align-items: center;
    margin: 0 0.5rem;
    background-color: var(--search-bar-color);
    border-radius: 1rem;
  }
  .search-bar-container button {
    height: 2rem;
    padding: 0 0.5rem;
    margin: 0;
    border: none;
    background-color: transparent;
  }
  .search-bar {
    height: 2rem;
    background-color: transparent;
    border: none;
    outline: 0;
  }
  .user-sm-image img {
    width: 4rem;
    height: 4rem;
  }
  .logout-user{
    cursor: pointer;
    font-size: 1.5rem;
    background-color: transparent;
    color:var(--text-color);
  }
  .logout-user:hover{
    background-color: var(--background-color);
    color:var(--text-color);
  }
  @media only screen and (max-width: 900px) {
    .search-bar-container {
      width: 10rem;
    }
  }
  @media only screen and (max-width: 540px) {
    .logo-text{
      font-size: 1.5rem;
    }
    .logo-image{
      width:3rem;
      height:3rem;
    }
    .user-image {
      display: none;
      flex: 0 1;
    }
    .user-image-container {
      flex: 0 1;
    }
    .details-section1-container {
      display: none;
    }
    .search-bar-container {
      width: 5rem;
    }
  }
.sidebar-btn{
    background-color:transparent;
    color:var(--light-color);   
}
.main-content-container{
    box-shadow: var(--shadow-color) 0px 7px 29px 0px;
}
.rules-page-container {
  display: grid;
  grid-template-rows: 6.25rem auto;
}
.rules-page-image-container img {
  max-width: 25rem;
  max-height: 15rem;
  margin: auto;
}
.quiz-image-details-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
  max-width: 60rem;
}
.rules-page-image-container {
  grid-column-start: 1;
  grid-column-end: 2;
  overflow: hidden;
  border-radius: 1rem;
}
.quiz-details-container {
  grid-column-start: 2;
  grid-column-end: 3;
}
.quiz-info {
  width: 6.25rem;
  display: inline-block;
}
.start-button {
  border-radius: 1rem;
  padding: 0.5rem 2.5rem;
  background-color: var(--accent-color);
  color: var(--light-color);
  display: inline-block;
}
.start-button:hover{
  background-color:var(--light-color);
  color:var(--accent-color);
  border-radius:1rem;
}
@media only screen and (max-width: 500px) {
  .quiz-image-details-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr auto;
  }
  .main-content-container {
    margin: 1rem;
  }
}

.question-page-container{
    display: grid;
    grid-template-rows: 6.25rem auto; 
}
.question-page-image-container{
    overflow: hidden;
    border-radius:1rem;
}
.radio-btn{
    margin-right: 1rem;
    cursor:pointer;
}

.result-page-container{
    display: grid;
    grid-template-rows: 6.25rem auto; 
}
.correct-answer{
    color:var(--success-color);
}
.wrong-answer{
    color:var(--error-color);
}
.list-container{
    padding-left: 1.2rem;
}
.retake-quiz-btn{
    border-radius:1rem;
    padding:0.6rem 2.5rem;
    background-color:var(--accent-color);
    color:var(--light-color);
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    font-size : 1rem;
    cursor:pointer;
    transition: all ease 0.3s;
}



.signup-container,
.signin-container,
.forgot-password-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.form-background-image {
  width: 40%;
  z-index: -1;
}
.blob-top-left {
  position: fixed;
  top: -100px;
  left: -120px;
}
.blob-bottom-right {
  position: fixed;
  bottom: -100px;
  right: -100px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.form-heading {
  color: var(--accent-color);
}
.authentication-details-container {
  max-width: 53.12rem;
  width: 100%;
  border-radius: 1rem;
  background-color: var(--secondary-color);
  transition: all 1s ease;
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  box-shadow: -3px -3px 5px var(--shadow-color1),
    3px 3px 5px var(--shadow-color2);
  z-index: 2;
}
.authentication-image-container {
  width: 50%;
}
.authentication-image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.wrapper {
  padding-left: 2rem;
  padding-right: 2rem;
  align-items: center;
}
.form-components-container {
  flex: 1 1;
}
.signup-form-container {
  width: 100%;
}
.password-field-container,
.email-field-container {
  position: relative;
}
.password-eye-icon,
.email-icon {
  position: absolute;
  top: 50%;
  right: 0.5rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: var(--light-grey);
}
.orange-button:hover {
  background-color: var(--light-color);
  color: var(--primary-color);
  transition: all ease 0.3s;
}

@media only screen and (max-width: 550px) {
  .authentication-details-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 1rem;
  }
  .form-components-container {
    width: 90%;
  }
}

